export default function SectionFour(){



    return<>
    <section className='sec_4'> 
         <div>
             <p> Below are few of my own developed <span> Web application. </span>   These are based on React, Nextjs, Nodejs, Typescript and Javascript.</p>
        </div>
    </section>
    {projects.map(project => {
        return(<Projects key={project.name} projects={project} />)
    })}
    </>
}




const Projects = ({ projects }) => {
    return(<>
        <section className='project_container'> 
        <div className='projects'>
             <h1> {projects.name} </h1>
             <p> {projects.base}</p>
             <a href={projects.link}>Visit</a>
        </div>
        <img src={projects.image } alt='projects' />
    </section>
    <section className='space'></section>
    </>)
}
